.tooltip {
  visibility: hidden;
  background-color: #636262;
  color: #fff;
  text-align: center;
  padding: 0.4rem;
  border-radius: 0.4rem;
  position: absolute;
  z-index: 1;
  font-size: 19px;
  bottom: 20%;
  /* 툴팁을 이미지 위에 표시하도록 설정 */
  left: 40%;
  margin-left: -5rem;
  /* 툴팁이 이미지 가운데에 표시되도록 설정 */
  opacity: 0;
  transition-duration: 0.7s;
}

.image-container7:hover .tooltip {
  visibility: visible;
  opacity: 1;
  bottom: 40%;
}

.image-container6:hover .tooltip {
  visibility: visible;
  opacity: 1;
  bottom: 40%;
}

.image-container5:hover .tooltip {
  visibility: visible;
  opacity: 1;
  bottom: 40%;
}

